@tailwind base;
@tailwind components;
@tailwind utilities;



@font-face {
  font-family: "Bamko";
  src: local("Bamko"),
    url("./fonts/bamko.otf") format("opentype");
  font-weight: normal;
}

@font-face {
  font-family: "Joaqueen";
  src: url("../public/fonts/Joaqueen.otf");
}

@font-face {
  font-family: "HelloParisSerif-Regular";
  src: url("../public/fonts/HelloParisSerif-Regular.ttf");
}
@font-face {
  font-family: "Bryson";
  src: url("../public/fonts/Bryson.otf");
}

@font-face {
  font-family: "Querygrand";
  src: url("../public/fonts/Querygrand.otf");
}

body {
  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Poppins', 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
